<template>
  <div>
    <h3>{{ titulo }}</h3> 
    <img alt="Dibujo bomba" src="../assets/bomba.png" width="180" v-show="mostrar">
    <img alt="Dibujo explosion" src="../assets/explosion.png" width="180" v-show="!mostrar">
    <button @click="comprobarContador" class="boton" v-show="mostrar">
      Cuenta atrás: {{ count }}
    </button>
  </div>
</template>

<script>
export default {
  name: 'cuenta-atras',
  props:{
    titulo: String
  },
  emits:['explosion'],
  data(){
    return{
      count:10,
      mostrar: true
    }
  },
  methods:{
    comprobarContador(){
      this.count--;
      if (this.count == 0){
        this.$emit('explosion', this.titulo);
        this.mostrar=false;
      }
    }
  }
  
}
</script>

